import './Chat.scss';
import { ChatMessages } from '../ChatMessages/ChatMessages';
import { ChatInput } from '../ChatInput/ChatInput';
import { useChats } from '../App/App';
import { Link, useParams } from 'react-router-dom';
import '../../styles/photo.scss';
import '../../styles/icon-back.scss';
import { useRef } from 'react';
import { ScrollButton } from '../ScrollButton/ScrollButton';

export const Chat: React.FC = () => {
  const { chats, save } = useChats();
  const { chatId } = useParams();
  const fieldRef = useRef<HTMLDivElement>(null);
  const selectedChat = chats.find(chat => chat.id === chatId) || null;

  return (
    <>
    <div className="Chat">
      {selectedChat !== null && (
        <>
          <div className="Chat__header">
            <Link
              to="/"
              className='icon-back Chat__icon-back' 
            />

            <div className="photo Chat__header-photo photo--ticked">
              <img
                className='photo__img'
                alt={selectedChat.userData.name}
                src={selectedChat.userData.avatar}
              />
            </div>

            <div className="Chat__header-name">
              {selectedChat.userData.name} 
            </div>
          </div>

          <div className="Chat__selected-container"> 
            {selectedChat.messages.map(message => (
              <ChatMessages
                key={message.id}
                message={message} 
                userData={selectedChat.userData}
                fieldRef={fieldRef} 
              />
            ))}

            <ScrollButton fieldRef={fieldRef} />
          </div>

          <ChatInput 
            save={save} 
            chats={chats} 
            selectedChatId={selectedChat.id} 
            fieldRef={fieldRef}
          />
        </>
      )}  
    </div>
    </>
  )
}